  margin-bottom: 10%;
  text-align: center; }
  .individual-project h2 {
    color: #7AFFFD;
    margin-bottom: 30px; }
  .individual-project p {
    text-align: left; }
  .individual-project p.note {
    font-style: italic;
    font-size: 16px;
    margin: 0 2%;
    word-wrap: break-word; }
    @media (min-width: 768px) {
      .individual-project p.note {
        font-size: 18px;
        margin: 0 15%; } }
  .individual-project .project-img {
    z-index: 1000;
    margin: 5% auto; }
    .individual-project .project-img img {
      max-width: 100%;
      max-height: 100%; }

/**********************
*******Container*******
***********************/
.bg-image {
  background-image: url("../assets/images/background/fade_top_bottom.jpg");
  background-repeat: no-repeat;
  background-position: right center;
  background-attachment: fixed;
  background-size: cover;
  overflow: auto;
  width: 100%;
  height: 100%;
  grid-row: 2 / -2;
  grid-column: 1 / -1;
  opacity: 0.2;
  z-index: -1; }



